export default {
	data() {
		return {
			book: {},
			review: '',
			title: '',
			author: {}
		}
	},
	methods: {

	},
	onLoad(e) {
		console.log(e)
		uni.request({
			url: "http://47.102.212.210:5000/review/" + e.code,
			success: (res) => {
				this.review = res.data.data.content
				this.book = res.data.data.book
				this.author = res.data.data.author
				this.title = res.data.data.title
			}
		})
	}
}
